import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from "@/components/Home"
import Login from "@/components/Login"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:"/Home",
      name:"Home",
      component:Home
    },
    {
      path:"/Login",
      name:"Login",
      component:Login
    },
    {
      path:"/HelloWorld",
      name:"HelloWorld",
      component:HelloWorld
    }
  ]
})



